@import "../variables.scss";

.stepper {
	background-color: $up;
	width: 100vw;
	position: absolute;
	left: 0;
	top: 0;
	border-radius: 0 4rem 4rem 0;

	&__completed {
		transition: 0.5s width cubic-bezier(0.075, 0.82, 0.165, 1);
		background: $primary;
		border-radius: 0 4rem 4rem 0;
		display: flex;
		justify-content: flex-end;
		width: 0%;

		&__p {
			color: $up;
			text-align: center;
			font-weight: 500;
			font-size: 1rem;
			padding: 0.75rem;
			border-radius: 4rem;
			margin: 0;
		}
	}
}
